import PostPage from '../../../page';

interface RouteParams {
  slug: string;
  month: string;
  category: string;
  post: string;
}

export default function PostByPathPage({ params }: { params: RouteParams }) {
  // Reaproveita a página de post existente usando o slug real (último segmento)
  return <PostPage params={{ slug: params.post }} />;
}

